import React, { Component } from 'react';
import Plot from 'react-plotly.js';
import ReactDOM from "react-dom";
import firebase from 'firebase';

class MyPlotUsage extends Component {
    constructor (props){
        super(props);

        this.state = {
            collection: this.props.collection || "realtime_data",
            doc: this.props.doc || "singleValues",
            plotWidth: '200',
            trace1: {
                values: [19, 26, 55],
                labels: ['On Peak', 'Mid Peak', 'Off Peak'],
                type: 'pie',
                marker: {
                    colors: [
                        '#bf0000',
                        '#ffbf00',
                        '#91cf51',
                    ]
                  },
         }
        }
         this.gotNewPlotData = this.gotNewPlotData.bind(this);
         this.AddToInitialData = this.AddToInitialData.bind(this);
    }
    
    AddToInitialData(x, y){
        var newX = this.state.plotDataX.slice();
        var newY = this.state.plotDataY.slice();

        newX.push(x);
        newY.push(y);

        this.setState(() => ({
            plotDataX: newX,
            plotDataY: newY
        }));
    }

    AddEntry(){
        const db = firebase.firestore();

        db.collection(this.state.collection).add({
            timestamp: firebase.firestore.Timestamp.fromDate(new Date()),
            actual: Math.floor(Math.random()*10)
        })
        .then(function(docRef) {
            console.log("Document written with ID: ", docRef.id);
        })
        .catch(function(error) {
            console.error("Error adding document: ", error);
        });
    }

    gotNewPlotData(docData){
        var newY1 = [
            docData['uPieOn'],
            docData['uPieMid'],
            docData['uPieOff'],
        ];


        this.setState((prevState, props) => ({
            trace1:  {
                x: prevState.trace1.x,
                y: newY1,
                type: prevState.trace1.type,
                name: 'Off Peak',
            },
        }));
    }

    componentDidMount(){
        const db = firebase.firestore();
        const gotNewPlotData = this.gotNewPlotData;

        db.collection(this.state.collection).doc(this.state.doc).get().then(document =>{
            gotNewPlotData(document.data());
        });

        db.collection('realtime_data').onSnapshot(snapshot =>{
            let changes = snapshot.docChanges();
            
            changes.forEach(change => {
                if(change.type != 'modified') return;
                gotNewPlotData(change.doc.data());
            });
        });

    }

    render() {
        return (
            <div >
                <Plot
                    data={[this.state.trace1]}
                    layout={ 
                        {
                        width:250,
                        height:200,
                        title: 'Lifetime Usage',
                        titlefont: {
                            family: 'Roboto, sans-serif',
                            size: 14,
                            color: 'BCBCBC'
                        },
                        showlegend: false,
                            legend: {
                            x: 0.75,
                            y: 1,
                            font: {
                              family: 'Roboto, sans-serif',
                              size: 9,
                              color: '#BCBCBC'
                            },
                            bgcolor: 'rgba(0,0,0,0)',
                            bordercolor: 'rgba(0,0,0,0)',
                            borderwidth: 0
                            },
                        plot_bgcolor: '#141414',
                        paper_bgcolor: '#141414',
                        autosize: false,
                        margin: {
                            l: 20,
                            r: 20,
                            b: 24,
                            t: 30,
                            pad: 4
                            },

                            xaxis: {
                                tickfont: {
                                    family: 'Roboto, sans-serif',
                                    size: 8.5,
                                    color: 'BCBCBC'
                                },
                                
                              },
                              yaxis: {
                                title: 'Total Cost ($)',
                                titlefont: {
                                    family: 'Roboto, sans-serif',
                                    size: 12,
                                    color: 'BCBCBC'
                                },
                                tickfont: {
                                    family: 'Roboto, sans-serif',
                                    size: 11,
                                    color: 'BCBCBC'
                                },
                                showgrid: true,
                                zeroline: true,
                                showline: true,
                                mirror: 'ticks',
                                gridcolor: '#242424',
                                gridwidth: 1,
                                zerolinecolor: '#242424',
                                zerolinewidth: 1,
                                linecolor: '#242424',
                                linewidth: 1
                              }
                        } }
                    config={ {displayModeBar: false, responsive:true} }
                />
          </div>
        );
    }
}

export default MyPlotUsage;